<template>
    <div class="container">
        <div class="main__users" v-if="users">
            <div class="main__users-item" v-for="user in users" :key="user.id">
                <p class="first__text">{{ user.username }}</p>
                <p >{{ user.email }}</p>
            </div>
        </div>
        <div v-else>
            Идет загрузка...
        </div>
    </div>
</template>

<script>
    export default {
       data() {
            return {
              
            }
       },
       created() {
        this.getUsers()
       },
       computed:{
        users(){
            return this.$store.state.users
        }
       },
       methods: {
        getUsers(){
            this.$store.dispatch('getUsers')
        }  
       },
    }
</script>